body {
  text-align: center;
  background-color: #7c7c7c;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

a:link, a:visited {
  color: (rebeccapurple);
  text-decoration: none;
  cursor:pointer;
}

a:link:active, a:visited:active {
  color: (rebeccapurple);
}

h3 {
  color: chartreuse;
}

.nav-bar{
  display: flex;
  justify-content: space-evenly;
}